import 'uni-pages';wx.__webpack_require_UNI_MP_PLUGIN__ = __webpack_require__;

import Vue from 'vue'
import App from './App'
import './styles/global.scss'

// 注册过滤器的最佳实践
import * as filters from './filters/index.js'
Object.keys(filters).forEach(key => {
	Vue.filter(key, filters[key])
})

Vue.config.productionTip = false

App.mpType = 'app'

const app = new Vue({
    ...App
})
app.$mount()